import React from "react";



export const ShareIcon = (props) => {
    const { className, ...rest } = props;
    return <svg className={className} width="23" height="23" viewBox="0 0 23 23" fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect width="23" height="23" fill="url(#pattern40)"/>
        <defs>
            <pattern id="pattern40" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use href="#image40" transform="scale(0.00195312)"/>
            </pattern>
            <image id="image40" width="512" height="512" href="data:image/png;base64,"/>
        </defs>
    </svg>

}

